@media only screen and (max-width: 768px) {
  /* adjust styles for screens with a maximum width of 768px */
  .booking-form {
    margin: 0 auto;
    width: 100%;
    padding: 0 20px;
  }
  .form-group {
    margin-bottom: 20px;
  }
}


@media only screen and (max-width: 768px) {
  .form-header h1 {
    font-size: 2em;
  }
  
  .form-group {
    margin-bottom: 1.5em;
  }
}

.booking-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.col-md-6 {
  width: 50%;
  padding-right: 15px;
  padding-left: 15px;
}

@media only screen and (max-width: 768px) {
  .col-md-6 {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
  }
}
